"use client"
import React from "react";
import Input from "../components/ui/Input";
import BackArrow from "../components/ui/BackArrow";

const page = () => {
    const cartItems = useSelector((state: any) => state.cart.items);

  // إجمالي الطلب (قبل التوصيل والخصم)
  const orderTotal = cartItems
    .reduce((sum: number, item: any) => sum + parseFloat(item.price) * item.quantity, 0)
    .toFixed(2);

  const deliveryFee = 10; // خدمة التوصيل
  const discount = 0; // كود الخصم

  const finalTotal = (parseFloat(orderTotal) + deliveryFee - discount).toFixed(2);
return (
    <div className="py-20  relative h-screen flex flex-col justify-between px-4">
      <div className="flex flex-col gap-8">
        <BackArrow />
        <div className="mt-8">
          <Input
            isLocation
            headline="كافهينو"
            placeholder="المعادي - شارع النصر"
          />
          <h2
        style={{ ...TEXT_STYLES.READEX_PRO.SEMIBOLD.H5, color: COLORS.TEXT }}
        className="mb-6"
      >
        الطلبات
      </h2>

      <div className="space-y-4">
        {cartItems.map((item: any) => (
          <div key={item.id} className="flex items-center justify-between">
            <div className="flex items-center flex-row-reverse gap-4">
              <Image
                src={coffee}
                alt={item.name}
                width={40}
                height={40}
                className="rounded-full"
              />
              <div className="flex flex-col text-right">
                <span style={{ ...TEXT_STYLES.READEX_PRO.SEMIBOLD.H6, color: COLORS.TEXT }}>
                  {item.name}
                </span>
                <span style={{ ...TEXT_STYLES.READEX_PRO.MEDIUM.H7, color: COLORS.GRAY }}>
                  {item.price} جنيه
                </span>
              </div>
            </div>
            <span className="font-bold text-green-600">X{item.quantity}</span>
          </div>
        ))}
      </div>

      {/* تفاصيل الفاتورة */}
      <h2
        style={{ ...TEXT_STYLES.READEX_PRO.SEMIBOLD.H5, color: COLORS.TEXT }}
        className="mt-10 mb-6"
      >
        تفاصيل الفاتورة
      </h2>

      <div className="space-y-2">
        <div className="flex justify-between flex-row-reverse">
          <span>إجمالي الطلب</span>
          <span>{orderTotal} جنيه</span>
        </div>
        <div className="flex justify-between flex-row-reverse">
          <span>خدمة التوصيل</span>
          <span>{deliveryFee.toFixed(2)} جنيه</span>
        </div>
        <div className="flex justify-between flex-row-reverse">
          <span>كود الخصم</span>
          <span>{discount} جنيه</span>
        </div>
        <div className="flex justify-between flex-row-reverse font-bold text-lg mt-2">
          <span>الإجمالي</span>
          <span>{finalTotal} جنيه</span>
        </div>
      </div>

      {/* زر الطلب */}
      <div className="mt-10">
        <Button isMain text="اطلب الان" />
      </div>
        </div>
      </div>
    </div>
  );
};

export default page;
